.container {
  @include container(15);
}

.sr-only {
  @include sr-only();
}

body {
  @include safe-font($font-family);
  font-weight: 300;
  font-size: 16px;
  line-height: 1.7;
  color: #fff;
  letter-spacing: 0.03em;
  background-color: $bg;
  @include font-media(16, 16);
}

.title {
  font-weight: 400;
  line-height: 1.2;
  font-size: 50px;
  text-transform: uppercase;
  @include font-media(50, 24);
}

.btn-bg {
  padding: 0;
  line-height: 1;
  color: $bg;
  background-color: #fff;
  @include btn-bg-actions();
}

