html,
body {
  display: flex;
  flex-direction: column;
}

.main {
  flex-grow: 1;
}

.container {
  max-width: 1270px;
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

body {
  font-family: "MerriweatherSans", "Helvetica", "Arial", sans-serif;
  font-weight: 300;
  font-size: 16px;
  line-height: 1.7;
  color: #fff;
  letter-spacing: 0.03em;
  background-color: #070a01;
}

.title {
  font-weight: 400;
  line-height: 1.2;
  font-size: 50px;
  text-transform: uppercase;
}

.btn-bg {
  padding: 0;
  line-height: 1;
  color: #070a01;
  background-color: #fff;
  transition: color 0.3s, box-shadow 0.3s, background-color 0.3s ease-in-out;
}

.btn-bg:hover {
  color: #fff;
  box-shadow: 0 0 0 1px #fff;
  background-color: transparent;
}

.btn-bg:focus {
  color: #070a01;
  background-color: #fff;
}

.btn-bg:active {
  color: #fff;
  background-color: transparent;
}

.header__nav {
  padding: 39px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transform: translate(0px, 120%);
  opacity: 0;
  transition: transform 0.8s, opacity 0.8s ease-in-out;
}

.header__nav.active {
  transform: translate(0px, 0px);
  opacity: 1;
  transition: all 0.8s ease 0;
}

.header__logo {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  font-size: 14px;
  text-transform: uppercase;
}

.logo__img {
  margin-right: 30px;
}

.menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.menu__item:first-child {
  margin-right: 45px;
}

.menu__link {
  font-size: 14px;
  text-transform: uppercase;
  position: relative;
}

.menu__link::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 1px;
  left: 0;
  bottom: 0;
  background-color: #fff;
  transition: opacity 0.3s ease-in-out;
}

.menu__link:hover:after {
  opacity: 0;
}

.hero__box {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.hero__title {
  font-weight: 400;
  font-size: 200px;
  line-height: 1.2;
  letter-spacing: normal;
  transform: translate(0px, 120%);
  opacity: 0;
  transition: transform 0.8s, opacity 0.8s ease-in-out;
}

.hero__title.active {
  transform: translate(0px, 0px);
  opacity: 1;
  transition: all 0.8s ease 0.4s;
}

.hero__text {
  font-size: 20px;
  max-width: 470px;
  margin-bottom: 67px;
  transform: translate(120%);
  opacity: 0;
  transition: transform 0.8s, opacity 0.8s ease-in-out;
}

.hero__text.active {
  transform: translate(0%);
  opacity: 1;
  transition: all 0.8s ease 0.8s;
}

.hero__btn {
  padding: 15px 115px;
  text-transform: uppercase;
  transform: translate(0px, 200%);
  opacity: 0;
  transition: color 0.3s, box-shadow 0.3s, background-color 0.3s, transform 0.8s, opacity 0.8s ease-in-out;
}

.hero__btn.active {
  transform: translate(0px, 0px);
  opacity: 1;
  transition: color 0.3s, box-shadow 0.3s, background-color 0.3s, transform 0.8s, opacity 0.8s ease 1.2s;
}

.hero__img {
  max-width: 556px;
  width: 100%;
  box-shadow: 9px 38px 30px 0px #0c0e03;
  transform: translate(200%);
  opacity: 0;
  transition: transform 0.8s, opacity 0.8s ease-in-out;
}

.hero__img.active {
  transform: translate(0%);
  opacity: 1;
  transition: all 0.8s ease 1.2s;
}

.quotes {
  padding: 134px 0;
}

.quotes__figure {
  max-width: 749px;
  margin: 0 auto;
  margin-bottom: 119px;
  transform: translate(0, 120%);
  opacity: 0;
  overflow: hidden;
  transition: transform 0.5s, opacity 0.5s ease-in-out;
}

.quotes__figure.active {
  transform: translate(0px, 0px);
  opacity: 1;
}

.quotes__text {
  text-transform: uppercase;
  margin-bottom: 20px;
}

.quotes__author {
  font-size: 14px;
  text-transform: uppercase;
}

.quotes__description {
  display: flex;
  max-width: 749px;
  margin: 0 auto;
}

.quotes__img {
  max-width: 398px;
  margin-right: 31px;
  transform: scale(0);
  transition: all 0.8s ease 0s;
}

.quotes__img.active {
  opacity: 1;
  transform: scale(1);
}

.quotes__detail {
  max-width: 288px;
  word-spacing: 0px;
  line-height: 1.5;
}

.quotes__detail p:nth-child(1) {
  margin-bottom: 15px;
  transform: translate(0, 120%);
  opacity: 0;
  overflow: hidden;
  transition: transform 0.5s, opacity 0.5s ease-in-out;
}

.quotes__detail p:nth-child(1).active {
  transform: translate(0%);
  opacity: 1;
  transition: all 0.8s ease 0.4s;
}

.quotes__detail p:nth-child(2) {
  display: block;
  transform: translate(0, 120%);
  opacity: 0;
  overflow: hidden;
  transition: transform 0.5s, opacity 0.5s ease-in-out;
}

.quotes__detail p:nth-child(2).active {
  transform: translate(0%);
  opacity: 1;
  transition: all 0.8s ease 0.8s;
}

body.compensate-for-scrollbar {
  overflow: hidden;
}

.fancybox-active {
